  <template>
  <!-- 搜索页面 -->
  <div class="Shopping">
    <header class="header">
      <van-icon name="arrow-left" @click="GoBack()"/>
      <h4>{{cities.name}}</h4>
      <p>
        <router-link to="/home">切换城市</router-link>
      </p>
    </header>
    <main class="main">
      <div class="city">
        <div class="city-input">
          <van-cell-group>
          <van-field v-model="cityval" placeholder="输入学校、商务楼、地址" />
          </van-cell-group>
          <!-- <el-input v-model="cityval" placeholder="输入学校、商务楼、地址" clearable v-popover:popover2></el-input> -->
        </div>
        <div class="city-button">
          <van-button type="info" @click="submit()">提交</van-button>
        </div>
      </div>
      <div class="getpois_ul" v-if="search.length>0">
        <ul>
          <li v-for="(v,i) in search" :key="i" @click="CitygoMsite(v)">
            <p>{{v.name}}</p>
            <span>{{v.address}}</span>
          </li>
        </ul>
      </div>
      <div v-else>
        <div class="pois">
          <p>搜索历史</p>
        </div>
        <div class="getpois">
          <ul>
            <!-- 点击跳转商家页面 -->
            <li v-for="(v,i) in getpois" :key="i" @click="CitygoMsite(v)">
              <p>{{v.name}}</p>
              <span>{{v.address}}</span>
            </li>
          </ul>
          <div class="del" v-if="getpois.length>0" @click="del()">清空所有</div>
        </div>
      </div>
    </main>
  </div>
</template>

<script>
export default {
  data() {
    return {
      cityval: "", //input内容
      cities: [], //传递id的城市数据内容
      search: [], //搜索内容
      getpois: [] //搜索历史
    };
  },
  methods: {
    //返回上次路由
    GoBack() {
      this.$router.go(-1);
    },

    //提交
    submit() {
      // this.$refs.inputVal.focus();
      if (this.cityval == "") {
        this.open();
      } else {
        //点击搜索地址数据内容
        this.$http
          .get(
            `http://elm.cangdu.org/v1/pois?type=search&city_id=${this.cities.id}&keyword=${this.cityval}`
          )
          .then(res => {
            // console.log(res);
            this.search = res.data;
          });
      }
    },

    // 当点击搜索内容为空时弹出框
    open() {
      this.$toast({
                message: "请输入内容",
                position: "bottom"
                  })  
    },

    //跳转到点击的附件商家
    CitygoMsite(v) {
      // console.log(v);
      let getpois = [...this.getpois]; //拷贝搜索历史的内容
      let check = getpois.some(val => {
        return val.geohash == v.geohash;
      });
      if (check == false) {
        getpois.push(v);
      }
      // console.log(check);

      localStorage.setItem("info", JSON.stringify(getpois));
      console.log(getpois);

      this.getpois = getpois;
      this.$router.push({ path: `/msite`});
      // console.log(check);
      this.$store.commit("dizhi",v.geohash)
    },

    //删除搜索历史
    del() {
      this.getpois = [];
      localStorage.setItem("info", JSON.stringify(this.getpois));
      // console.log(this.getpois);
    }
  },
  mounted() {
    //传递id的城市数据内容
    let id = this.$route.params.id;
    this.$http.get(`http://elm.cangdu.org/v1/cities/${id}`).then(res => {
      this.cities = res.data;
    });
    let obj = JSON.parse(localStorage.getItem("info"));
    // console.log(obj);

    if (obj != null) {
      this.getpois = obj;
    }
  }
};
</script>

